.compare {
  margin-bottom: 92px;

  @include onTablet {
    margin-bottom: 88px;
  }

  @include onDesktop {
    margin-bottom: 112px;
   }
}

.comparing {
  color: $color-white;

  &__photo {
    width: 100%;
    height: 100%;
    display: block;
    transition: transform 0.5s ease;

    &:hover {
      transform: scale(110%);
    }
  }

  &__link {
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    margin: 0;
    padding-bottom: 8px;
    text-decoration: none;
    color: $color-white;
    transition: 0.5s;

    &:hover {
      color: $color-links;
    }
  }

  &__paragraf {
    margin: 0;
    font-size: 16px;
    line-height: 22px;
  }

  &__price {
    font-size: 24px;
    line-height: 34px;
    font-weight: bold;
  }

}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 24px;

  @include onphone {
    grid-template-columns: 1fr;
    column-gap: 24px;
  }

  @include onTablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }

  @include onDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }


  &__item--1 {

    @include onTablet  {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 1 / 5 ;
    }
  }


  &__item--2 {

    @include onTablet  {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 5 / 9;
    }

  }

  &__item--3 {

    @include onTablet  {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 9 / 13;
    }
 }
}


